.search .ant-card {
  margin-top: 5px;
}
.logout {
  color: #1890ff;
  cursor: pointer;
  transition: all 0.3s;
  margin: 0px 5px;
}
.logout:hover {
  margin: 0px 5px;
  color: #40a9ff;
  cursor: pointer;
}
.logout-danger {
  margin: 0px 5px;
  color: #ff3333;
  transition: all 0.3s;
}
.logout-danger:hover {
  margin: 0px 5px;
  color: #ff3300;
  cursor: pointer;
}
