body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.content-blcok-card {
  margin-bottom: 24px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eaeaea;
}
.content-blcok-card h2 {
  font-size: 15px;
  height: 42px;
  font-weight: bold;
  line-height: 38px;
  color: #686868;
}
.search-box {
  margin-left: 15px;
}
.search-box .search-button {
  width: 180px;
  margin-right: 5px;
}
.search-box .search-button .ant-btn-primary {
  border: 1px solid #d9d9d9;
  background: #fff;
  color: #9d9d9d;
}
.customer-name-column div {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 200px;
  width: 100%;
}
.update-time {
  color: #ff6827;
  font-size: 12px;
}
.antd-card {
  min-height: 800px;
  background: #fff;
}
#root .antd-pro-charts--chart-card-total {
  font-size: 27px;
}
#root .antd-pro-charts--chart-card-total em {
  font-size: 16px;
}
#root .antd-pro-charts--chart-card-contentFixed .card-content .title {
  font-size: 1.2em !important;
}
#root .antd-pro-charts--chart-card-contentFixed .card-content .subtotal-title,
#root .antd-pro-charts--chart-card-contentFixed .card-content .antd-pro-trend-trendItem {
  font-size: 1.1em !important;
}
#root .flag-up .antd-pro-charts--chart-card-total,
#root .flag-up .antd-pro-trend-trendItem {
  color: #f41e28;
}
#root .flag-up .antd-pro-charts--chart-card-total .anticon-caret-up,
#root .flag-up .antd-pro-trend-trendItem .anticon-caret-up {
  width: 13px;
  height: 10px;
  position: relative;
}
#root .flag-up .antd-pro-charts--chart-card-total .anticon-caret-up::before,
#root .flag-up .antd-pro-trend-trendItem .anticon-caret-up::before {
  position: absolute;
  top: -11px;
  left: 2px;
  font-size: 14px;
}
#root .flag-up .antd-pro-charts--chart-card-total .anticon-caret-up::after,
#root .flag-up .antd-pro-trend-trendItem .anticon-caret-up::after {
  content: '';
  position: absolute;
  top: -3px;
  width: 4px;
  height: 18px;
  background: #f41e28;
}
#root .flag-down .antd-pro-charts--chart-card-total,
#root .flag-down .antd-pro-trend-trendItem {
  color: #49bc19;
}
#root .flag-down .antd-pro-charts--chart-card-total .anticon-caret-down,
#root .flag-down .antd-pro-trend-trendItem .anticon-caret-down {
  width: 13px;
  height: 10px;
  position: relative;
}
#root .flag-down .antd-pro-charts--chart-card-total .anticon-caret-down::before,
#root .flag-down .antd-pro-trend-trendItem .anticon-caret-down::before {
  position: absolute;
  top: 8px;
  left: 2px;
  font-size: 14px;
}
#root .flag-down .antd-pro-charts--chart-card-total .anticon-caret-down::after,
#root .flag-down .antd-pro-trend-trendItem .anticon-caret-down::after {
  content: '';
  position: absolute;
  top: -5px;
  width: 4px;
  height: 18px;
  background: #49bc19;
}
@media screen and (min-width: 800px) and (max-width: 1300px) {
  #root .antd-pro-charts--chart-card-total {
    font-size: 27px;
  }
  #root .antd-pro-charts--chart-card-contentFixed .card-content .title {
    font-size: 13px !important;
  }
  #root .antd-pro-charts--chart-card-contentFixed .card-content .subtotal-title,
  #root .antd-pro-charts--chart-card-contentFixed .card-content .antd-pro-trend-trendItem {
    font-size: 13px !important;
  }
}
body ::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
body ::-webkit-scrollbar-thumb {
  background: #e7e7e7;
}
body ::-webkit-scrollbar-track {
  background: #f8f8f8;
}
