.content-blcok-card {
  margin-bottom: 24px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eaeaea;
}
.content-blcok-card h2 {
  font-size: 15px;
  height: 42px;
  font-weight: bold;
  line-height: 38px;
  color: #686868;
}
.search-box {
  margin-left: 15px;
}
.search-box .search-button {
  width: 180px;
  margin-right: 5px;
}
.search-box .search-button .ant-btn-primary {
  border: 1px solid #d9d9d9;
  background: #fff;
  color: #9d9d9d;
}
.customer-name-column div {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 200px;
  width: 100%;
}
.update-time {
  color: #ff6827;
  font-size: 12px;
}
.antd-card {
  min-height: 800px;
  background: #fff;
}
#root .antd-pro-charts--chart-card-total {
  font-size: 27px;
}
#root .antd-pro-charts--chart-card-total em {
  font-size: 16px;
}
#root .antd-pro-charts--chart-card-contentFixed .card-content .title {
  font-size: 1.2em !important;
}
#root .antd-pro-charts--chart-card-contentFixed .card-content .subtotal-title,
#root .antd-pro-charts--chart-card-contentFixed .card-content .antd-pro-trend-trendItem {
  font-size: 1.1em !important;
}
#root .flag-up .antd-pro-charts--chart-card-total,
#root .flag-up .antd-pro-trend-trendItem {
  color: #f41e28;
}
#root .flag-up .antd-pro-charts--chart-card-total .anticon-caret-up,
#root .flag-up .antd-pro-trend-trendItem .anticon-caret-up {
  width: 13px;
  height: 10px;
  position: relative;
}
#root .flag-up .antd-pro-charts--chart-card-total .anticon-caret-up::before,
#root .flag-up .antd-pro-trend-trendItem .anticon-caret-up::before {
  position: absolute;
  top: -11px;
  left: 2px;
  font-size: 14px;
}
#root .flag-up .antd-pro-charts--chart-card-total .anticon-caret-up::after,
#root .flag-up .antd-pro-trend-trendItem .anticon-caret-up::after {
  content: '';
  position: absolute;
  top: -3px;
  width: 4px;
  height: 18px;
  background: #f41e28;
}
#root .flag-down .antd-pro-charts--chart-card-total,
#root .flag-down .antd-pro-trend-trendItem {
  color: #49bc19;
}
#root .flag-down .antd-pro-charts--chart-card-total .anticon-caret-down,
#root .flag-down .antd-pro-trend-trendItem .anticon-caret-down {
  width: 13px;
  height: 10px;
  position: relative;
}
#root .flag-down .antd-pro-charts--chart-card-total .anticon-caret-down::before,
#root .flag-down .antd-pro-trend-trendItem .anticon-caret-down::before {
  position: absolute;
  top: 8px;
  left: 2px;
  font-size: 14px;
}
#root .flag-down .antd-pro-charts--chart-card-total .anticon-caret-down::after,
#root .flag-down .antd-pro-trend-trendItem .anticon-caret-down::after {
  content: '';
  position: absolute;
  top: -5px;
  width: 4px;
  height: 18px;
  background: #49bc19;
}
@media screen and (min-width: 800px) and (max-width: 1300px) {
  #root .antd-pro-charts--chart-card-total {
    font-size: 27px;
  }
  #root .antd-pro-charts--chart-card-contentFixed .card-content .title {
    font-size: 13px !important;
  }
  #root .antd-pro-charts--chart-card-contentFixed .card-content .subtotal-title,
  #root .antd-pro-charts--chart-card-contentFixed .card-content .antd-pro-trend-trendItem {
    font-size: 13px !important;
  }
}
body ::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
body ::-webkit-scrollbar-thumb {
  background: #e7e7e7;
}
body ::-webkit-scrollbar-track {
  background: #f8f8f8;
}
body {
  height: 100%;
  background: #f0f2f5;
}
#preloader {
  display: none;
}
.layout-container {
  position: relative;
  height: 100%;
}
.layout-aside {
  width: 200px;
  height: 100%;
  overflow: hidden;
  position: relative;
  background-color: #F3F3F3;
  transition-duration: 0.2s;
}
.layout-aside[data-mini='0'] {
  width: 65px;
}
.pmt-back-img {
  margin-left: -30px;
  width: calc(100% + 48px);
  height: calc(100vh - 120px);
  background-image: url('//sf1-ttcdn-tos.pstatp.com/obj/ttfe/cg/crm_oversea/ocrm.png');
}
#root div .antd-pro-page-header-extraContent {
  min-width: 200px;
}
#root .ant-layout {
  height: 100vh;
  background: #f0f2f5;
  display: block;
}
#root .ant-layout .logo {
  height: 32px;
  background: #333;
  border-radius: 6px;
  margin: 16px;
}
#root .ant-layout .ant-layout-sider {
  margin-top: 50px;
  background: #001529;
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
}
#root .ant-layout .custom-header {
  height: 50px;
  background: #fff;
  padding: 0;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  position: fixed;
  z-index: 1000;
  width: 100%;
  left: 0;
}
#root .ant-layout .custom-content {
  margin-top: 0px;
  padding: 0 18px;
}
#root .ant-layout .custom-content[data-atmaster='true'] {
  margin-top: 0;
}
#root .ant-layout-container {
  clear: both;
}
#root .ant-layout-container .gutter-box {
  padding: 8px 0;
}
#root .ant-layout .footer {
  height: 48px;
  line-height: 48px;
  text-align: center;
  font-size: 12px;
  color: #999;
  background: none;
  margin: 5px 0 0 0;
  padding: 0;
}
#root .ant-input-group-addon {
  background-color: #fff;
}
#root .antd-pro-ellipsis-lineClamp {
  line-height: 1.2;
}
